<template>
  <van-pull-refresh v-model="refreshing" @refresh="onRefresh" class="real-main">
    <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad">
      <div class="cell" v-for="item in list" :key="item.id">
        <p>{{item.time}}</p>
        <van-cell :title="item.title"
          :label="item.text" />
      </div>
    </van-list>
  </van-pull-refresh>
</template>

<script>
  import {
    List,
    PullRefresh,
    Cell
  } from 'vant';

  export default {
    components: {
      [List.name]: List,
      [Cell.name]: Cell,
      [PullRefresh.name]: PullRefresh,
    },

    data() {
      return {
        list: [],
        loading: false,
        finished: false,
        refreshing: false
      };
    },

    methods: {
      onLoad() {
        setTimeout(() => {
          if (this.refreshing) {
            this.list = [];
            this.refreshing = false;
          }

          for (let i = 0; i < 10; i++) {
            this.list.push({
              id:this.list.length,
              title:'新冠肺炎心理热线27日启用 北师大心理咨询师提供服务',
              text:`1月26日，针对疫情，中国移动北京公司接到北京师范大学心理学部紧急开通400热线的业务需求后，立刻积极响应，第一时间开展工作，当天即完成平台上线与开通任务。
北京师范大学心理学部针对新冠肺炎开通心理支持热线4001-888-976，旨在为全国需要心理支持的民众提供线上心理疏导服务。心理热线陪伴为一对一服务，该服务适用于急需情绪疏导和心理支持的人群。
据了解，热线电话均由经验丰富的咨询师接听，每次30分钟左右。热线电话开放时间为每天6时至24时，该热线将于1月27日早晨6点准时启用。中国移动北京公司为该平台提供专业通信支撑，可保障150部热线在不同时段提供心理健康支持。`,
              time:"2020-10-11"
            });
          }
          this.loading = false;

          if (this.list.length >= 40) {
            this.finished = true;
          }
        }, 1000);
      },
      onRefresh() {
        // 清空列表数据
        this.finished = false;

        // 重新加载数据
        // 将 loading 设置为 true，表示处于加载状态
        this.loading = true;
        this.onLoad();
      }
    }
  };
</script>

<style lang="less" scoped>
  .real-main {
    padding: 10px;

    .cell {
      >p {
        //margin-left: 15px;
        color: rgba(69, 90, 100, 0.6);
      }

      margin-bottom: 5px;
    }
  }

  .van-cell:not(:last-child)::after {
    border-bottom: initial;
  }
</style>
